import React, { useState, useEffect } from 'react';
import LongPressButton from './LongPressButton.js';
import Selector from '../forms/FunctionalSelector.js';
import getKey from '../utils/KeyGenerator.js';
import ScoreCard from './ScoreCard.js';

const SurfScores = ({ players, heatLength, isWinner, getSurferIndexWithPriority, shiftPriority, losePriority/*, setSurfScore*/}) => {

    const [timerOn, setTimerOn] = useState(false);
    const [timesUp, setTimesUp] = useState(false);
    const [time, setTime] = useState(60 * 60); // Initial time in seconds (e.g., 1 hour)
   
    useEffect(() => {
        let intervalId;
        if (timerOn) {
            intervalId = setInterval(() => {
                setTime((prevTime) => {
                    const newTime = prevTime - 1;
                    if (newTime <= 0) {
                        clearInterval(intervalId);
                        setTimesUp(true);
                        return 0;
                    }
                    return newTime;
                });
            }, 1000);
        }

        return () => clearInterval(intervalId);
    }, [timerOn]);

    useEffect(() => {
        if (timesUp) {
            setTimerOn(false);
            setTime(heatLength);
            console.log(`heatLength: ${heatLength}`)
            setTimesUp(false);
        }
    }, [timesUp]);

    const toggleTimer = () => {
        if (timerOn) {
            setTimerOn(false)
        } else {
            setTimerOn(true)
        }
    }

    const surfClass = (playerIndex) => 'flex3Column p-20 size25 bold' + ((isWinner(playerIndex)) ? ' color-neogreen shakingShaka mt-20' : '');

    const getTimerButtonClasses = () => {
        const timerButtonClasses = timerOn ? 'bg-red' : 'bg-green';
        return timerButtonClasses;
    }
    const getTimerClasses = () => {
        const timerClasses = (time < 120) ? 'blinking-fade' : '';
        return timerClasses;
    }
    const formatTime = (timeInSeconds) => {
        const hours = Math.floor(timeInSeconds / 3600);
        const minutes = Math.floor((timeInSeconds % 3600) / 60);
        const seconds = timeInSeconds % 60;
        const formattedHours = hours.toString().padStart(2, '0');
        const formattedMinutes = minutes.toString().padStart(2, '0');
        const formattedSeconds = seconds.toString().padStart(2, '0');
        return `${formattedHours}:${formattedMinutes}:${formattedSeconds}`;
    };
    const getJerseyColor = (surferIndex) => {
        if (players[surferIndex].surfJerseyColor === undefined) {
            const newPlayers = [...players];
            newPlayers[surferIndex].surfJerseyColor = surferIndex;
            localStorage.setItem('players', JSON.stringify(newPlayers))
            return 0;
        }
        return players[surferIndex].surfJerseyColor;
    }
    const getPriorityColor = (priorityIndex) => {
        const surferIndex = getSurferIndexWithPriority(priorityIndex);
        const jerseyColorIndex = (surferIndex === null) ? null : getJerseyColor(surferIndex);
        const color = (jerseyColorIndex === null) ? 'white' : jerseyColors[jerseyColorIndex];
        return color;
    }
    const jerseyColors = ['white', 'yellow', 'neogreen', 'black', 'blue', 'orange', 'pink', 'red'];
    const getPriorityItem = (index) => {
            return <div className={`button flex${players.length}Column r-10 m-5 w-75 size40 pt-20 pb-20 bg-${getPriorityColor(index)}`} key={getKey(`priorityFlag`)} onClick={() => losePriority(index)}>
                        {index}
                    </div>
    }
    /*
    const shiftPriority = () => {
        const playerId = getSurferIndexWithPriority(1);
        const surfScores = players[playerId].surfScores;
        let scoreIndex = 9;
        setSurfScore(playerId, scoreIndex, 'prio')
    }
    */
    const getPriorityDisplay = () => {
        return <div>
            <div className='centeredContent lite bold size25 p-10 bg-dark r-10 ml-10 mr-10 mt-10 mb-15'>
                Priority
            </div>
            <div className='flexContainer ml-5 mr-5 mt--10 color-dark text-outline-light' >
                {players.map((player, index) => getPriorityItem(index+1))}
            </div>
        </div>
    }

    return <React.Fragment>
        <div className='flexContainer p-5 bg-dark r-10 m-10'>
            <div className={`flex2Column m-5 r-10 bold size20 p-20 color-light ${getTimerButtonClasses()}`} onClick={() => toggleTimer()}>
                {(timerOn) ? 'STOP' : 'START'}
            </div>
            <div className='flex2Column m-5 r-10 bg-dkBlue bold size25 p-20 color-light'>
                <span className={getTimerClasses()}>
                    {formatTime(time)}
                </span>
            </div>
        </div>
        {getPriorityDisplay()}
    </React.Fragment>

}

export default SurfScores;